import {
	GET_POSTS,
	GET_POST,
	ADD_POST,
	DELETE_POST,
	GET_ERRORS,
	POST_LOADING,
	CLEAR_ERRORS,
} from './types';

import axios from 'axios';

export const addPost = (postData) => (dispatch) => {
	dispatch(clearErrors());

	axios
		.post('/api/posts/', postData)
		.then((res) =>
			dispatch({
				type: ADD_POST,
				payload: res.data,
			})
		)
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const addComment = (newComment, postId) => (dispatch) => {
	dispatch(clearErrors());
	axios
		.post(`/api/posts/comment/${postId}`, newComment)
		.then((res) =>
			dispatch({
				type: GET_POST,
				payload: res.data,
			})
		)
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const getPosts = () => (dispatch) => {
	dispatch(setPostLoading());
	axios
		.get('/api/posts/')
		.then((res) =>
			dispatch({
				type: GET_POSTS,
				payload: res.data,
			})
		)
		.catch((err) =>
			dispatch({
				type: GET_POSTS,
				payload: null,
			})
		);
};

export const getPost = (id) => (dispatch) => {
	dispatch(setPostLoading());
	axios
		.get(`/api/posts/${id}`)
		.then((res) =>
			dispatch({
				type: GET_POST,
				payload: res.data,
			})
		)
		.catch((err) =>
			dispatch({
				type: GET_POST,
				payload: null,
			})
		);
};

export const deletePost = (id) => (dispatch) => {
	if (
		window.confirm('You are about to delete. This action cannot be undone.')
	) {
		axios
			.delete(`/api/posts/${id}`)
			.then((res) =>
				dispatch({
					type: DELETE_POST,
					payload: id,
				})
			)
			.catch((err) =>
				dispatch({
					type: GET_ERRORS,
					payload: err.response.data,
				})
			);
	}
};

export const deleteComment = (postId, commentId) => (dispatch) => {
	if (
		window.confirm('You are about to delete. This action cannot be undone.')
	) {
		axios
			.delete(`/api/posts/comment/${postId}/${commentId}`)
			.then((res) =>
				dispatch({
					type: GET_POST,
					payload: res.data,
				})
			)
			.catch((err) =>
				dispatch({
					type: GET_ERRORS,
					payload: err.response.data,
				})
			);
	}
};

export const addLikeFromFeed = (id) => (dispatch) => {
	axios
		.post(`/api/posts/like/${id}`)
		.then((res) => dispatch(getPosts()))
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const addLikeFromPost = (id) => (dispatch) => {
	axios
		.post(`/api/posts/like/${id}`)
		.then((res) => dispatch(getPost(id)))
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const removeLikeFromFeed = (id) => (dispatch) => {
	axios
		.post(`/api/posts/unlike/${id}`)
		.then((res) => dispatch(getPosts()))
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const removeLikeFromPost = (id) => (dispatch) => {
	axios
		.post(`/api/posts/unlike/${id}`)
		.then((res) => dispatch(getPost(id)))
		.catch((err) =>
			dispatch({
				type: GET_ERRORS,
				payload: err.response.data,
			})
		);
};

export const setPostLoading = () => {
	return { type: POST_LOADING };
};

export const clearErrors = () => {
	return { type: CLEAR_ERRORS };
};
